.portfolio {
	background: $secondary url('../img/bg-portfolio.png') repeat-y center left;
	background-size: 50%;
	line-height: 1.4;
	padding-top: 70px;
	&__title {
		max-width: 790px;
		margin: 0 auto;
		margin-bottom: 100px;
		font-size: 30px;
		@include bold; }
	&__arrows {
		max-width: 120px;
		margin: auto;
		display: flex;
		justify-content: space-between;
		margin-top: 68px;
 } }		// cursor: pointer Почему тут просто не задать? Зачем столько разделений?
@media (max-width: 1200px) {
	.slider {
		&__item {
			margin-right: 15px; } } }
@media (max-width: 768px) {
	.slider {
		&__image {
			margin: auto; } } }
@media (max-width: 320px) {
	.portfolio__title {
		font-size: 18px; }
	.slick-slide img {
		margin-left: 7px; } }
