.steps {
	&__title {
		max-width: 500px;
		margin: auto;
		margin-bottom: 59px; }
	&__subtitle {
		text-align: center;
		margin-bottom: 58px; }
	&__item {
		margin-top: 105px; } }
.items {
	display: flex;
	text-align: center; }
.item {
	flex-basis: 20%;
	position: relative; }
.item:after {
	content: '';
	background-image: url('../img/arrow-curve.png');
	width: 72px;
	height: 25px;
	position: absolute;
	top: -10%;
	right: -15%;
 }	// margin-left: 70px
.item:last-child {
	&:after {
		display: none; } }
.item__image {
	border: 3px solid $main-color;
	width: 100px;
	height: 100px;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	margin: auto;
	margin-bottom: 30px; }
.item__text {
	max-width: 160px;
	font-size: 14px;
	margin: auto; }
@media (max-width: 1200px) {
	.steps__item {
		flex-basis: 50%; }
	.item__text {
		margin-bottom: -75px; }
	.item:after {
		display: none; }
	.items {
		justify-content: center; } }
@media (max-width: 768px) {
	.items {
		margin-top: -117px; }
	.items__step {
		flex-direction: column; }
	.item__text {
		margin-bottom: -75px; }
	.item:after {
		display: none; } }



