.price {
	&__title {
		margin: auto;
		margin-bottom: 58px;
		max-width: 553px;
		font-size: 30px;
		line-height: 1.5;
		padding-bottom: 2px;
		letter-spacing: 1px; }
	&__subtitle {
		margin-bottom: 59px;
		text-align: center;
		font-size: 20px; }
	&__card {
		margin-bottom: 30px; } }
.card {
	text-align: center;
	border: 1px solid #e8e8e8;
	transition: 0.2s;
	&:hover {
		box-shadow: 0 20px 25px rgba(0,0,0,0.1);
		.card__image:before {
			opacity: 1; } }
	&__text {
		padding: 40px; }
	&__title {
		margin-top: -6px;
		font-size: 18px; }
	&__image {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(to top, #ffd200 0%, rgba(255,255,255,0) 100%);
			opacity: 0;
			transition: 0.2s; } }
	&__link {
		display: inline-block;
		@include bold;
		text-decoration: none;
		color: $text-color;
		font-size: 16px;
		margin-top: 8px;
		position: relative;
		&:hover {
			&:after {
				height: 12px; } }
		&:after {
			content: '';
			position: absolute;
			bottom: -4px;
			left: 0;
			display: block;
			width: 100%;
			height: 3px;
			background-color: $main-color;
			z-index: -1;
			transition: 0.2s; } }
	&s {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; } }
.image-display {
	display: block; }
@media (max-width: 768px) {
	.cards {
		justify-content: center; } }
@media (max-width: 320px) {
	.price {
		&__title {
			font-size: 18px; }
		&__subtitle {
			font-size: 14px; } }
	.card__title {
		font-size: 14px; }
	.card__link {
		font-size: 16px; } }
